import React, { Suspense } from "react";
import SplitText from "./blocks/TextAnimations/SplitText/SplitText";
import { Link } from "react-scroll";
import "./styles/App.css";
import "./styles/Skills.css";
import "./styles/3Dmodels.css";
import "./styles/ImageRenders.css";
import "./styles/VideoRender.css";
import "./styles/ContactSection.css";

// Lazy-load sections and components
const ModelsSection = React.lazy(() => import("./components/ModelsSection"));
const ImageRenders = React.lazy(() => import("./components/ImageRenders"));
const VideoRender = React.lazy(() => import("./components/VideoRender"));
const ContactSection = React.lazy(() => import("./components/ContactSection"));
const SlideUpText = React.lazy(() => import('./components/Textreveal'));

// Lazy-load background lights
const BackgroundLight = React.lazy(() => import("./components/BackgroundLight"));
const TitleBG = React.lazy(() => import("./components/TitleBG"));
const Skills = React.lazy(() => import("./components/Skills"));

const App = () => {
  return (
    <div className="container">
      {/* Hero Section */}
      <section className="hero" id="hero">
        <Suspense fallback={<div>Loading...</div>}>
          <BackgroundLight 
            initialX={6.0}
            color={{ r: 1.0, g: 0.5, b: 0.1 }}
            glowRadiusX={0.9} 
            glowRadiusY={0.36}
            intensity={0.5} 
            opacity={0.5}
            fadeDuration={2.0}
            lightDirection={1.2}
            wiggleAmount={100}
            verticalWiggleAmount={100}
            wiggleSpeed={8}
          />
        </Suspense>
        <Suspense fallback={<div>Loading...</div>}>
          <TitleBG />
        </Suspense>
        <div className="hero-content">
          <h1 className="first-line">
            <SplitText text="DISCOVER" animation="fadeIn" delay={50} />
          </h1>
          <h2 className="second-line">
            <SplitText text="THIRD DIMENSION!" animation="fadeIn" delay={50} />
          </h2>
          <Link 
            to="skills"
            smooth={true}
            duration={500}
            offset={-70}
          >
            <Suspense fallback={<div>Loading...</div>}>
              <SlideUpText text="Scroll down to explore" bottom="150px" left="30px" />
            </Suspense>
          </Link>
        </div>
      </section>

      {/* Section Divider */}
      <div className="section-divider-hero">
        <h2 className="section-line-hero">It's</h2>
        <h2 className="section-line-hero">Versatile</h2>
        <h3 className="section-subline-hero">Scroll down for skill section</h3>
      </div>

      {/* Skills Section */}
      <section id="skills">
        <Suspense fallback={<div>Loading...</div>}>
          <BackgroundLight 
            initialX={-5.0}
            color={{ r: 0.1, g: 0.5, b: 1.0 }}
            glowRadiusX={0.4}
            glowRadiusY={0.3}
            intensity={0.4}
            opacity={0.5}
            fadeDuration={2.0}
            lightDirection={-0}
            wiggleAmount={100}
            verticalWiggleAmount={100}
            wiggleSpeed={8}
          />
        </Suspense>
        <Suspense fallback={<div>Loading...</div>}>
          <Skills />
        </Suspense>
      </section>

      {/* Models Section */}
      <section id="models">
        <Suspense fallback={<div>Loading...</div>}>
          <BackgroundLight 
            initialX={6.0}
            color={{ r: 1.0, g: 0.5, b: 0.1 }}
            glowRadiusX={0.9}
            glowRadiusY={0.36}
            intensity={0.5}
            opacity={0.5}
            fadeDuration={2.0}
            lightDirection={1.2}
            wiggleAmount={100}
            verticalWiggleAmount={100}
            wiggleSpeed={8}
          />
        </Suspense>
        <Suspense fallback={<div>Loading models...</div>}>
          <ModelsSection />
        </Suspense>
      </section>

      {/* Image Renders Section */}
      <section id="image-renders">
        <Suspense fallback={<div>Loading images...</div>}>
          <BackgroundLight 
            initialX={-5.0}
            color={{ r: 0.1, g: 0.5, b: 1.0 }}
            glowRadiusX={0.4}
            glowRadiusY={0.3}
            intensity={0.4}
            opacity={0.5}
            fadeDuration={2.0}
            lightDirection={-0}
            wiggleAmount={100}
            verticalWiggleAmount={100}
            wiggleSpeed={8}
          />
        </Suspense>
        <Suspense fallback={<div>Loading...</div>}>
          <ImageRenders />
        </Suspense>
      </section>

      {/* Video Renders Section */}
      <section id="video-renders">
        <Suspense fallback={<div>Loading videos...</div>}>
          <BackgroundLight 
            initialX={6.0}
            color={{ r: 1.0, g: 0.5, b: 0.1 }}
            glowRadiusX={0.9}
            glowRadiusY={0.36}
            intensity={0.5}
            opacity={0.5}
            fadeDuration={2.0}
            lightDirection={1.2}
            wiggleAmount={100}
            verticalWiggleAmount={100}
            wiggleSpeed={8}
          />
        </Suspense>
        <Suspense fallback={<div>Loading...</div>}>
          <VideoRender />
        </Suspense>
      </section>

      {/* Contact Section */}
      <section id="contact">
        <Suspense fallback={<div>Loading...</div>}>
          <BackgroundLight 
            initialX={-5.0}
            color={{ r: 0.1, g: 0.5, b: 1.0 }}
            glowRadiusX={0.4}
            glowRadiusY={0.3}
            intensity={0.4}
            opacity={0.5}
            fadeDuration={2.0}
            lightDirection={-0}
            wiggleAmount={100}
            verticalWiggleAmount={100}
            wiggleSpeed={8}
          />
        </Suspense>
        <Suspense fallback={<div>Loading contact section...</div>}>
          <ContactSection />
        </Suspense>
      </section>
    </div>
  );
};

export default App;
